import React from "react";
import imageCrm from "../images/image_crm.png";
import imageWallet from "../images/image_wallet.png";
import "./styles/ProjectItem.css";
import Subtitle from "./Subtitle";

class ProjectItem extends React.Component {
  render() {
    return (
        <div className="list-inline-item item project-wrapper col-md-6 col-12">
          <img
            className="ProjectItem__image card-img-top border"
            src={this.props.project.image}
            alt="Card image cap"
          />

          <div className="project-info card ProjectItem__border-card-footer">
            {/* <small>Tipo proyecto</small> */}

            <h3 className="text-center">
              <p href="project-detail.html">
                <span>{this.props.project.title}</span>
                {/* <i className="fa fa-angle-right project-icon"></i> */}
              </p>
              {this.props.project.techs.map((tech, index) => {
                return (
                  <React.Fragment key={index}>
                    <span class="badge badge-pill ProjectItem__badge">
                      {tech}
                    </span>
                  </React.Fragment>
                );
              })}
            </h3>
          </div>
        </div>
    );
  }
}

class Project extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      projects: [],
    };
  }
  componentDidMount() {
    this.setState({
      projects: [
        {
          title: "Aplicación movil para la gestion de clientes",
          image: imageCrm,
          status: "Concluido",
          techs: ["Java", "XML", "volley", "API", "AdobeXD", "MVC"],
        },
        {
          title: "Aplicación movil para finanzas personal",
          image: imageWallet,
          status: "En desarrollo",
          techs: ["Kotlin", "XML", "ROOM", "MVP", "Retrofit"],
        },
      ],
    });
  }
  render() {
    return (
      <section className="project section-padding" id="project">
        <div className="container-fluid">
          <Subtitle subtitle="Portafolio"></Subtitle>
          <div className="row">
            <div className="col-lg-12 col-12">
              <div className="ProjectList__container">
                <ul class="list-inline">
                  {this.state.projects.map((project) => {
                    return (
                      <React.Fragment key={project.id}>
                        <ProjectItem project={project}></ProjectItem>
                      </React.Fragment>
                    );
                  })}
                </ul>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default Project;
