import React from "react";
import imageProfile from "../images/profile.jpeg";
import './styles/Hero.css';

class Hero extends React.Component {
  render() {
    return (
      <section className="hero hero-bg d-flex justify-content-center align-items-center">
        <div className="container text-center">
          <div className="row">
            <div className="col-sm-6 col-12">
              <div className="hero-image">
                <img
                  src={imageProfile}
                  className="Hero__image-profile img-fluid rounded"
                  alt="working girl"
                />
              </div>
            </div>
            <div className="col-sm-6 col-12 d-flex flex-column justify-content-center align-items-center">
              <div className="hero-text">
                <h1 className="text-white">Hola soy Gonzalo López</h1>
                <h2 className="text-white">Desarrollador móvil</h2>

                <a href="contact.html" className="custom-btn btn-bg btn mt-3">
                  Contáctame
                </a>

                <strong className="d-block py-3 text-white">
                  <i className="fa fa-phone mr-2"></i> 080 070 4224
                </strong>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default Hero;
