import React from "react";
import Subtitle from "./Subtitle";

class About extends React.Component {
  render() {
    return (
      <section className="about section-padding" id="about">
        <div className="container">
          <Subtitle subtitle="Sobre mí"></Subtitle>
          <div className="row">
            <div className="col-lg-7 mx-auto col-md-10 col-12">
              <div className="about-info">
                <p className="mb-0 text-justify">
                  Hola, soy gonzalo lopez programador de dia y de noche catador
                  de vino y de buen café.
                </p>
              </div>

              <div className="about-image">
                {/* <img src="images/office.png" className="img-fluid" alt="office"> */}
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default About;
